html {
  box-sizing: border-box;
  height: 100%;
  overflow: auto;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  margin: 0;
}


body {
  background-color: #fff;
  font-weight: 500;
}

.container {
  // max-width: $large-screen;
  padding: 0 3vmax;
  @media (min-width: $medium-screen) {
    padding: 0 4vmax;
  }
}

.narrow-container {
  padding: 0 3vmax;
  @media (min-width: $medium-screen) {
    padding: 0 4vmax;
  }
  max-width: $large-screen;
  margin: 0 auto;
}

.hidden-sm {
  @media (max-width: $medium-screen) {
    display: none !important;
  }
}