$translate: 3 * $base-spacing;

@media screen and (min-width: $medium-screen) {

  body.aos {

    @for $i from 0 through 50 {
      [data-aos][data-aos][data-aos-duration="#{50 * $i}"],
       &[data-aos-duration="#{50 * $i}"] [data-aos] {
        transition-duration: #{50ms * $i}, #{50ms * $i};
        transition-delay: 0ms, 0;
      }
      [data-aos][data-aos][data-aos-duration="#{50 * $i}"].aos-animate,
       &[data-aos-duration="#{50 * $i}"] [data-aos].aos-animate {
        transition-duration: #{50ms * $i};
        transition-delay: 0ms;
      }

      [data-aos][data-aos][data-aos-delay="#{50 * $i}"],
       &[data-aos-delay="#{50 * $i}"] [data-aos] {
        transition-delay: 0;

      }
      [data-aos][data-aos][data-aos-delay="#{50 * $i}"].aos-animate,
       &[data-aos-delay="#{50 * $i}"] [data-aos].aos-animate {
        transition-delay: #{50ms * $i}, #{50ms * $i};
      }
    }


    &[data-aos-easing=linear] [data-aos],
    [data-aos][data-aos][data-aos-easing=linear] {
      transition-timing-function: cubic-bezier(.25,.25,.75,.75);
    }

    &[data-aos-easing=ease] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease] {
      transition-timing-function: ease;
    }

    &[data-aos-easing=ease-in] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in] {
      transition-timing-function: ease-in;
    }

    &[data-aos-easing=ease-out] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-out] {
      transition-timing-function: ease-out;
    }

    &[data-aos-easing=ease-in-out] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-out] {
      transition-timing-function: ease-in-out;
    }

    &[data-aos-easing=ease-in-back] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-back] {
      transition-timing-function: cubic-bezier(.6,-.28,.735,.045);
    }

    &[data-aos-easing=ease-out-back] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-out-back] {
      transition-timing-function: cubic-bezier(.175,.885,.32,1.275);
    }

    &[data-aos-easing=ease-in-out-back] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-out-back] {
      transition-timing-function: cubic-bezier(.68,-.55,.265,1.55);
    }

    &[data-aos-easing=ease-in-sine] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-sine] {
      transition-timing-function: cubic-bezier(.47,0,.745,.715);
    }

    &[data-aos-easing=ease-out-sine] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-out-sine] {
      transition-timing-function: cubic-bezier(.39,.575,.565,1);
    }

    &[data-aos-easing=ease-in-out-sine] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-out-sine] {
      transition-timing-function: cubic-bezier(.445,.05,.55,.95);
    }

    &[data-aos-easing=ease-in-quad] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-quad] {
      transition-timing-function: cubic-bezier(.55,.085,.68,.53);
    }

    &[data-aos-easing=ease-out-quad] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-out-quad] {
      transition-timing-function: cubic-bezier(.25,.46,.45,.94);
    }

    &[data-aos-easing=ease-in-out-quad] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-out-quad] {
      transition-timing-function: cubic-bezier(.455,.03,.515,.955);
    }

    &[data-aos-easing=ease-in-cubic] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-cubic] {
      transition-timing-function: cubic-bezier(.55,.085,.68,.53);
    }

    &[data-aos-easing=ease-out-cubic] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-out-cubic] {
      transition-timing-function: cubic-bezier(.25,.46,.45,.94);
    }

    &[data-aos-easing=ease-in-out-cubic] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-out-cubic] {
      transition-timing-function: cubic-bezier(.455,.03,.515,.955);
    }

    &[data-aos-easing=ease-in-quart] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-quart] {
      transition-timing-function: cubic-bezier(.55,.085,.68,.53);
    }

    &[data-aos-easing=ease-out-quart] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-out-quart] {
      transition-timing-function: cubic-bezier(.25,.46,.45,.94);
    }

    &[data-aos-easing=ease-in-out-quart] [data-aos],
    [data-aos][data-aos][data-aos-easing=ease-in-out-quart] {
      transition-timing-function: cubic-bezier(.455,.03,.515,.955);
    }

    [data-aos^=fade][data-aos^=fade] {
      opacity: 0;
      transition-property: opacity,transform;
    }

    [data-aos^=fade][data-aos^=fade].aos-animate {
      opacity: 1;
      transform: translate(0);
    }

    [data-aos=fade-up] {
      transform: translateY($translate);
    }

    [data-aos=fade-down] {
      transform: translateY(-$translate);
    }

    [data-aos=fade-right] {
      transform: translate(-$translate);
    }

    [data-aos=fade-left] {
      transform: translate($translate);
    }

    [data-aos=fade-up-right] {
      transform: translate(-$translate,$translate);
    }

    [data-aos=fade-up-left] {
      transform: translate($translate,$translate);
    }

    [data-aos=fade-down-right] {
      transform: translate(-$translate,-$translate);
    }

    [data-aos=fade-down-left] {
      transform: translate($translate,-$translate);
    }

    [data-aos^=zoom][data-aos^=zoom] {
      opacity: 0;
      transition-property: opacity,transform;
    }

    [data-aos^=zoom][data-aos^=zoom].aos-animate {
      opacity: 1;
      transform: translate(0) scale(1);
    }

    [data-aos=zoom-in] {
      transform: scale(.6);
    }

    [data-aos=zoom-in-up] {
      transform: translateY($translate) scale(.6);
    }

    [data-aos=zoom-in-down] {
      transform: translateY(-$translate) scale(.6);
    }

    [data-aos=zoom-in-right] {
      transform: translate(-$translate) scale(.6);
    }

    [data-aos=zoom-in-left] {
      transform: translate($translate) scale(.6);
    }

    [data-aos=zoom-out] {
      transform: scale(1.2);
    }

    [data-aos=zoom-out-up] {
      transform: translateY($translate) scale(1.2);
    }

    [data-aos=zoom-out-down] {
      transform: translateY(-$translate) scale(1.2);
    }

    [data-aos=zoom-out-right] {
      transform: translate(-$translate) scale(1.2);
    }

    [data-aos=zoom-out-left] {
      transform: translate($translate) scale(1.2);
    }

    [data-aos^=slide][data-aos^=slide] {
      transition-property: transform;
    }

    [data-aos^=slide][data-aos^=slide].aos-animate {
      transform: translate(0);
    }

    [data-aos=slide-up] {
      transform: translateY(100%);
    }

    [data-aos=slide-down] {
      transform: translateY(-100%);
    }

    [data-aos=slide-right] {
      transform: translateX(-100%);
    }

    [data-aos=slide-left] {
      transform: translateX(100%);
    }

    [data-aos^=flip][data-aos^=flip] {
      backface-visibility: hidden;
      transition-property: transform;
    }

    [data-aos=flip-left] {
      transform: perspective(2500px) rotateY(-100deg);
    }

    [data-aos=flip-left].aos-animate {
      transform: perspective(2500px) rotateY(0);
    }

    [data-aos=flip-right] {
      transform: perspective(2500px) rotateY(100deg);
    }

    [data-aos=flip-right].aos-animate {
      transform: perspective(2500px) rotateY(0);
    }

    [data-aos=flip-up] {
      transform: perspective(2500px) rotateX(-100deg);
    }

    [data-aos=flip-up].aos-animate {
      transform: perspective(2500px) rotateX(0);
    }

    [data-aos=flip-down] {
      transform: perspective(2500px) rotateX(100deg);
    }

    [data-aos=flip-down].aos-animate {
      transform: perspective(2500px) rotateX(0);
    }
  }
}