.section--intro {
  height: 100vh;
  width: 100%;
  // background-image: linear-gradient(135deg, $color10 40%, white 40.01%)
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 4vmax;

  & > *:last-child {
    margin-bottom: 0; }
  .intro--container {
    text-align: center;
    padding: 8vmax;
    background-color: $light-green;
    position: relative;
    @media (orientation: portrait) {
      min-height: 88vh;
      display: flex;
      padding: 4vmax;
      flex-direction: column;
      justify-content: center; }
